}

.x-carousel {
  overflow: hidden;
  position: relative;
}
.x-carousel > .x-carousel-indicator {
  position: absolute;
  bottom: 10px;
}
.x-carousel > .x-carousel-indicator > a {
  float: left;
  margin-left: 6px;
}
.x-carousel > .x-carousel-indicator > a > .x-icon-dot {
  display: inline-block;
  vertical-align: middle;
  width: 6px;
  height: 6px;
  border-radius: 3px;
  background-color: #d0cdd1;
}
.x-carousel > .x-carousel-indicator > a > .x-icon-dot.active {
  background-color: #ffe26d;
}
.x-carousel > .x-carousel-indicator.x-indicator-left {
  left: 15px;
  right: auto;
}
.x-carousel > .x-carousel-indicator.x-indicator-center {
  right: 50%;
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
}
.x-carousel > .x-carousel-indicator.x-indicator-right {
  right: 15px;
}
.x-carousel > .x-carousel-box {
  overflow: hidden;
  position: relative;
}
.x-carousel > .x-carousel-box > .x-carousel-item {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.x-carousel > .x-carousel-box > .x-carousel-item > a {
  display: block;
  width: 100%;
  height: 100%;
}
.x-carousel > .x-carousel-box > .x-carousel-item > a > .x-carousel-img {
  display: block;
  width: 100%;
  height: 100%;
  background: center center no-repeat;
  background-size: cover;
}
.x-carousel > .x-carousel-box > .x-carousel-item > a > .x-carousel-desc {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 1.4em;
  font-size: 16px;
  padding: 20px 50px 12px 13px;
  margin: 0;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0, rgba(0, 0, 0, 0)), to(rgba(0, 0, 0, 0.7)));
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0, rgba(0, 0, 0, 0.7) 100%);
  color: #fff;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.5);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal;
}

.x-swiper {
  height: 100%;
  overflow: hidden;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.x-swiper > .x-swiper-item {
  height: 100%;
  background-position: center center;
  background-size: cover;
  position: relative;
  overflow: hidden;
  float: left;
  margin-top: -1px;
  padding-top: 1px;
  margin-left: -1px;
  padding-left: 1px;
}

.x-marquee {
  overflow: hidden;
}
.x-marquee-box {
  padding: 0;
  margin: 0;
  width: 100%;
  height: auto;
}
.x-marquee-box li {
  list-style-type: none;
  /* the default is disc in andriod webkit */
  margin: 0;
}
